:root {
  --font-family: "Raleway", sans-serif;
  --content-width: 1410px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-black: #001322;
  --color-ebony: #283642;
  --color-orange: #f15a25;
  --color-active: #f26a1c;
  --color-shadow: #907f53;
  --color-white: #fff;
  --color-silver: #bababa;
  --color-boulder: #7c7c7c;
  --br-r: 15px;
}

