@font-face {
  font-family: "Raleway";
  src: url("../fonts/../fonts/Raleway-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Raleway";
  src: url("../fonts/../fonts/Raleway-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Raleway";
  src: url("../fonts/../fonts/Raleway-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
.page {
  min-height: 100vh;
  font-family: var(--font-family);
  font-weight: 500;
  font-feature-settings: "pnum" on, "lnum" on;
  font-size: 18px;
  line-height: 1.65;
  letter-spacing: 0.03em;
  color: var(--color-white);
  background-color: var(--color-black);
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}
.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn {
  font-size: calc(12px + 6 * ((100vw - 320px) / 1600));
  position: relative;
  display: inline-flex;
  align-items: center;
  border: none;
  border-radius: 5px;
  padding: 1.125rem 4.75rem 1.125rem 2.625rem;
  line-height: 1.65;
  color: var(--color-white);
  background-color: var(--color-orange);
  transition-property: color, background-color;
  transition-duration: 0.3s;
  transition-timing-function: linear;
}
.btn::before {
  content: "";
  position: absolute;
  right: 44px;
  mask-image: url("../img/sprite.svg#cursor");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  width: 27px;
  height: 27px;
  background-color: var(--color-white);
}
.btn:focus {
  outline: 2px solid var(--color-blue);
  outline-offset: 2px;
}
.btn:hover {
  background-color: var(--color-active);
}
.btn:active {
  background-color: var(--color-active);
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
}

.social__list {
  display: flex;
}
.social__item:not(:last-child) {
  margin-right: 30px;
}
.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: var(--color-ebony);
  transition: background-color 0.3s linear;
}
.social__link::before {
  content: "";
  position: absolute;
  mask-image: url("../img/sprite.svg#fb");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  width: 20px;
  height: 20px;
  background-color: var(--color-boulder);
  transition: background-color 0.3s linear;
}
.social__link:hover {
  background-color: var(--color-orange);
}
.social__link:hover::before {
  background-color: var(--color-white);
}
.social__link--fb::before {
  mask-image: url("../img/sprite.svg#fb");
}
.social__link--ln::before {
  mask-image: url("../img/sprite.svg#ln");
}
.social__link--in::before {
  mask-image: url("../img/sprite.svg#in");
}
.social__link--yt::before {
  mask-image: url("../img/sprite.svg#yt");
}
.social__link--be::before {
  mask-image: url("../img/sprite.svg#be");
}

.text {
  font-size: calc(14px + 4 * ((100vw - 320px) / 1600));
  margin-bottom: 47px;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.burger {
  display: none;
  --burger-width: 30px;
  --burger-height: 30px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}
@media (max-width: calc(62em - 0.0625em)) {
  .burger {
    display: block;
  }
}

.dis-scroll {
  /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.stub {
  padding-top: 2.1875rem;
}
.stub__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 9.6875rem;
}
@media (max-width: calc(62em - 0.0625em)) {
  .stub__top {
    margin-bottom: 4.6875rem;
  }
}
@media (max-width: calc(36em - 0.0625em)) {
  .stub__top {
    flex-direction: column;
    gap: 40px;
  }
}
.stub__title {
  font-size: calc(55px + 95 * ((100vw - 320px) / 1600));
  font-weight: 900;
  line-height: 1.17;
  letter-spacing: 0.04em;
  margin-bottom: 2.875rem;
}
.stub__title::first-letter {
  text-transform: capitalize;
}
.stub__title .word {
  display: inline-flex;
}